import React from 'react'
import TplProject from '../../templates/template-project'
import PostTile1 from '../../tiles/posts/tile1'
import PostTile2 from '../../tiles/posts/tile2'
import PostTile3 from '../../tiles/posts/tile3'
import TileAvailable from '../../tiles/posts/tileAvailable'
import TileFeatured from '../../tiles/posts/tileFeatured'

const ProjectTpl2 = (props) => {
    const {project} = props;
    

    return (

        <TplProject project={project}>
            
            <div className="row">
                
                <PostTile1
                   post={project.posts[0]}
                   selected
                    />
               
                <PostTile2
                    post={project.posts[1]}
                    projectId={project.id}
                />
                
                
            </div>
            <div className="row ">
                <div className="row colspan">
                    <PostTile1
                        post={project.posts[2]}
                        projectId={project.id}
                    />
                    { project.posts[3].type === 6 &&
                        <TileFeatured /> 
                    }
                    { project.posts[3].type !== 6 &&
                         <PostTile1
                            post={project.posts[3]}
                            projectId={project.id}
                        />
                    }

                    <PostTile2
                       post={project.posts[5]}
                       projectId={project.id}
                    />
                </div>
                <PostTile3 
                    post={project.posts[4]}
                    projectId={project.id}
                />
            </div>
            <div className="row">
                <PostTile1
                        post={project.posts[6]}
                        projectId={project.id}
                    />
                 <PostTile2
                       post={project.posts[7]}
                       projectId={project.id}
                    />
               
            </div>
            <div className="row">
                <PostTile2
                        post={project.posts[8]}
                        projectId={project.id}
                    />
                 <PostTile1
                       post={project.posts[9]}
                       projectId={project.id}
                    />
                
               
            </div>

           

            


        <style jsx> 
        {`

        .colspan .cell {
            margin-bottom: 11px;
        }

        .content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: inherit;
        }

        .cell:last-of-type {
            margin-right: 0px;
        }
        .row:last-of-type {
            margin-bottom: 0px;
        }
        .cell {
            background-color: #F4F6F6;
            border: 1px solid #F0F2F2;
            border-radius: 5px;
            margin-right: 11px;
            height: 282px;

            color: #707070;
            letter-spacing: 0;
        }

        .selected {   
            background-color: #D3DDDD;
            border: 1px solid #4E7876;
            color: #000000;
            opacity: 0.5;
        }

        .cell:hover {
            
            opacity: 1;
        }

        .cell.size1 {
            width: 325px;
        }

        .cell.size2 {
            width: 663px;
        }

        .cell.size3 {
            width: 325px;
            height: 577px;
        }

        .cell.featured {
            border: 1px solid #C2CCCC;
        }
        .featured .header {
            border-bottom: 2px solid #D3DDDD;
        }

        .cell.available {
            border: 2px dashed #C2CCCC;
            background-color: #fff; 
            width: 323px;
            height: 280px;
        }
        
        .available .header {
            border-bottom: 2px dashed #D3DDDD;
        }

        .row {
            display: flex;
            margin-bottom: 11px;
        }
        
        .row.colspan { 
            flex-flow: wrap;
            width: 676px;
            margin-bottom: 0px !important;
        }

        .row.colspan .cell { 
            margin-bottom: 11px;
        }

        .last { 
            margin-bottom: 0px !important;
        }

        .header, .body, .footer {
            padding: 0px 28px 0px 28px;
        }


        .body {
            margin-top: 13px;
            font: 12px/17px Arial;
        }

        .actions {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
        }

        .footer {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        .header {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;

            height: 40px;
            border-bottom: 2px solid #FFFFFF;
            
            font: Bold 16px/19px Arial;
        }

        `}
            
        </style>

        </TplProject>
    )
}



export default ProjectTpl2;