import React from 'react'
import { useRouter } from 'next/router'
import {getMockProject} from '../../components/mocks/mocks'
import ProjectTpl1 from '../../components/templates/projects/tpl1'
import ProjectTpl2 from '../../components/templates/projects/tpl2'

const Project = () => {
    const router = useRouter()
    const { projectid } = router.query
    const project = getMockProject(projectid);
    

    return (<>
        { project.template === 1 &&
            <ProjectTpl1 project={project} />
        }
        { project.template === 2 &&
            <ProjectTpl2 project={project} />
        }
       </>
    )
}

Project.getInitialProps = async () => {
    return {};
  };

export default Project;